body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  background-color: #61dafb;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.main {
width: 60vw;  
height: 100vh;
margin: auto;
display: flex;
flex-direction: column;
align-items: center;
}

h1,h2{
  color: white;
}

.row1{
  flex-grow: 2;
  text-align: center;
  display: flex;
  align-items: center;
  padding-top: 2rem;
  font-family: 'Anton',cursive;
  font-size: 1.5rem;
}

.row2{
  flex-grow: 1;
  text-align: center;
  display: flex;
  align-items: center;
  font-family: 'Plus Jakarta Sans',cursive;
  font-size: 3rem;
}

.buttonMain{
  flex-grow: 1;
  width: 10rem;
  height: 2rem;
  border-radius: 1rem;
  background-color:#FDC913;
  font-size: 1rem;
  font-family: 'Plus Jakarta Sans',cursive;
  text-align: center;
  border-style: hidden;
}

.buttonMain:hover{
  background-color:#eb4264;
  color: white;

}


.img{
  flex-grow: 1;
  margin-top: 1rem;
  display: flex;
  align-items: flex-end;
}

.img img{
  width: 60vw;
}
.a {
  background-color: transparent;
  color: white;
  font-size: 0.9rem;
  font-family: 'Plus Jakarta Sans',cursive;
  text-align: center;
  border-style: hidden;
}

.a:hover{
  font-weight: bold;
  font-size: 0.92rem;

}
input[type=text], select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}
.gray {
  color:#5F5F5F;
}

.boldGray{
  color:#5F5F5F;
  font-weight: bold;
}

.adetButton {
background-color:#FDC913;
width:2.5rem;
height:3rem;
border: none;
border-radius: 0.4rem;
}
button{
width:100%;
background-color:#FDC913;
height:3rem;
border: none;
}
